/* eslint-disable no-undef */

import random from 'random';

import {
    getCashFlowPerUnitPerMonth,
    getCashOnCash,
    getTotalInvesment,
} from '../store';

const simVal = v => {
    let {
        dist,
        min_rng,
        max_rng,
        choice,
    } = v;

    if (dist === 'Normal') {
        min_rng = parseFloat(min_rng);
        max_rng = parseFloat(max_rng);
        return Math.max(random.normal((max_rng + min_rng) / 2,(max_rng-min_rng) / 2 / 1.96)(),0);
    }
    else if (dist === 'Uniform') {
        min_rng = parseFloat(min_rng);
        max_rng = parseFloat(max_rng);
        return random.uniform(min_rng,max_rng)();
    }
    else if (dist === 'Choice') {
        const choice_list = choice.split(";");
        const rnd = random.float();
        const len = choice_list.length;
        return choice_list[Math.floor(rnd*len)];
    }
    else {
        alert('Undefined distribution');
    }
};

const getParams = (params, key) => {
    return {
        val: params[key],
        dist: params[key+"_dist"],
        min_rng: params[key+"_min"],
        max_rng: params[key+"_max"],
        choice: params[key+"_choice"],
    };
};

const getSim = (params, key) => {
    const p = getParams(params, key);
    const {
        val,
        dist,
    } = p;
    return (dist === 'None') ? val : simVal(p);
};

onmessage = e => {
    const {
        data: {
            sims: {
                CashOnCash,
                GoodCashOnCash,
                CashFlowPerUnitPerMonth,
                TotalInvestment,
            } = {},
            params,
            params: {
                numUnits,
                propertyTaxFreq,
                insuranceFreq,
                mortgagePaymentsPerYear,
                mortgageYears,
                downPymt,
                loanToValue,
                brrrr,
            } = {},
        }
    } = e;

    postMessage({
        progress: 0,
        results: {},
    });

    const simsMod = {
        CashOnCash: Array(CashOnCash.length).fill(null),
        GoodCashOnCash: Array(GoodCashOnCash.length).fill(true),
        CashFlowPerUnitPerMonth: Array(CashFlowPerUnitPerMonth.length).fill(null),
        TotalInvestment: Array(TotalInvestment.length).fill(null),
    };

    let lastPct = 0;
    for (let i = 0; i < CashOnCash.length; i++) {
        const purchasePriceSim = getSim(params, "purchasePrice");
        const interestRateSim = getSim(params, "interestRate");
        const closingCostsSim = getSim(params, "closingCosts");
        const repairsSim = getSim(params, "repairs");
        const propertyTaxSim = getSim(params, "propertyTax");
        const insuranceSim = getSim(params, "insurance");
        const expenseUtilitiesSim = getSim(params, "expenseUtilities");
        const managementSim = getSim(params, "management");
        const leasingCostsSim = getSim(params, "leasingCosts");
        const avgOccupancySim = getSim(params, "avgOccupancy");
        const maintenanceSim = getSim(params, "maintenance");
        const incomePerUnitSim = getSim(params, "incomePerUnit");
        const vacancyRateSim = getSim(params, "vacancyRate");
        const incomeUtilitiesSim = getSim(params, "incomeUtilities");
        const afterRepairValueSim = getSim(params, "afterRepairValue");

        const [CoC, goodCoC] = getCashOnCash(incomePerUnitSim, numUnits, vacancyRateSim, managementSim, leasingCostsSim, avgOccupancySim, maintenanceSim, expenseUtilitiesSim, propertyTaxSim, propertyTaxFreq, insuranceSim, insuranceFreq, interestRateSim, mortgagePaymentsPerYear, mortgageYears, downPymt, purchasePriceSim, closingCostsSim, repairsSim, loanToValue, afterRepairValueSim, brrrr);
        if ((CoC > 0 && goodCoC && CoC !== Infinity) || (CoC < 0 && !goodCoC && CoC !== -Infinity)) {
            simsMod.CashOnCash[i] = CoC / 100;
        }
        else if ((CoC < 0 && goodCoC) || (CoC === Infinity)) {
            simsMod.CashOnCash[i] = 1;
        }
        else if ((CoC > 0 && !goodCoC) || (CoC === -Infinity)){
            simsMod.CashOnCash[i] = -1;
        }

        simsMod.GoodCashOnCash[i] = goodCoC;
        simsMod.CashFlowPerUnitPerMonth[i] = getCashFlowPerUnitPerMonth(incomePerUnitSim, numUnits, vacancyRateSim, managementSim, leasingCostsSim, avgOccupancySim, maintenanceSim, expenseUtilitiesSim, propertyTaxSim, propertyTaxFreq, insuranceSim, insuranceFreq, interestRateSim, mortgagePaymentsPerYear, mortgageYears, downPymt, purchasePriceSim, loanToValue, afterRepairValueSim, brrrr);
        simsMod.TotalInvestment[i] = getTotalInvesment(downPymt, purchasePriceSim, closingCostsSim, repairsSim, loanToValue, afterRepairValueSim, brrrr);

        const curPct = (i / CashOnCash.length);
        if (curPct - lastPct > 1) {
            postMessage({
                progress: curPct,
                results: {},
            });
            lastPct = curPct;
        }
    }

    postMessage({
        progress: 100,
        results: simsMod,
    });
};
