const forecastValue = (val, rate) => {
    const fcst_val = Array(30).fill(null);
    for (let i=0; i < 30; i++){
        fcst_val[i] = val * (1 + rate) ** (i);
    }
    return fcst_val;
};

const forecastMtgValue = (mtgPmtPerYr, mortgageYears) => {
    const fcst_val = Array(30).fill(null);

    for (let i=0;i < 30;i++){
        fcst_val[i] = (i < mortgageYears ? parseFloat(mtgPmtPerYr) : 0); // stop payments when loan concludes
    }
    return fcst_val;
};

const forecastAccumPrinc = (interestRate, mortgagePaymentsPerYear, years, downPymt, purchasePrice, loanToValue, afterRepairValue, brrrr) => {
    const fcst_val = Array(30).fill(null);
    for (let i=0; i < fcst_val.length; i++){
        fcst_val[i] = getAmountFinanced(downPymt, purchasePrice, loanToValue, afterRepairValue, brrrr) - getBalanceRemaining(interestRate, mortgagePaymentsPerYear, years, downPymt, purchasePrice, loanToValue, afterRepairValue, brrrr, i + 1);
    }
    return fcst_val;
};

const addElementwise = (arrOfArrs) => {
    const newArr = new Array(30).fill(null);
    for (let i=0; i < arrOfArrs[0].length; i++){
        for (let j=0; j < arrOfArrs.length; j++){
            newArr[i] += arrOfArrs[j][i];
        }
    }
    return newArr;
};

export const forecastRevenue = selector({
    key: "forecastRevenue",
    get: ({ get }) => {
        return forecastValue(getGrossRentalIncomePerMonth(get(incomePerUnitState), get(numUnitsState)) * 12, parseFloat(get(annRevIncState)) / 100);
    },
});

