export const forecastAccumPrincipal = selector({
    key: "forecastAccumPrincipal",
    get: ({ get }) => {
        return forecastAccumPrinc(get(interestRateState), get(mortgagePaymentsPerYearState), get(mortgageYearsState), get(downPaymentState), get(purchasePriceState), get(loanToValueState), get(afterRepairValueState), get(brrrrState));
    },
});

export const forecastEquityAccrued = selector({
    key: "forecastEquityAccrued",
    get: ({ get }) => {
        const arr = [];
        for (let i=0; i < get(forecastAccumPrincipal).length; i++){
            arr[i] = get(forecastAccumPrincipal)[i] - (i === 0 ? 0 : get(forecastAccumPrincipal)[i-1]);
        }
        return arr;
    },
});

export const forecastAppreciation = selector({
    key: "forecastAppreciation",
    get: ({ get }) => {
        const brrrr = get(brrrrState);
        const purch_price = parseFloat(get(purchasePriceState));
        const repairs = parseFloat(get(repairsState));
        const arv = parseFloat(get(afterRepairValueState));
        const appr_rate = parseFloat(get(annApprState)) / 100;
        const arr = Array(30).fill(0);

        arr[0] += (brrrr ? arv - purch_price : repairs); // forced appreciation from brrrr, get initial repair value back in the form of added value
        for (let i=1; i < arr.length; i++){
            arr[i] = (((1 + appr_rate) ** i) - (1 + appr_rate) ** (i - 1)) * (brrrr ? parseFloat(get(afterRepairValueState)) : (parseFloat(get(purchasePriceState)) + parseFloat(get(repairsState))));
        }
        return arr;
    },
});

export const forecastReinvCashFlows = selector({
    key: "forecastReinvCashFlows",
    get: ({ get }) => {
        const return_rate = parseFloat(get(annReinvRetState)) / 100;
        const arr = Array(30).fill(0);
        const cm_arr = Array(30).fill(0);
        const cf_arr = get(forecastCashFlow);

        cm_arr[0] = cf_arr[0];
        for (let i=1; i < arr.length; i++){
            cm_arr[i] = (1 + return_rate) * cm_arr[i-1]; // get return from current investment
            arr[i] = cm_arr[i] - cm_arr[i-1]; // current year's returns based on the difference before investing new cash flows
            cm_arr[i] += cf_arr[i]; // add new cash flows to investments
        }
        return arr;
    },
});

export const forecastTotalReturn = selector({
    key: "forecastTotalReturn",
    get: ({ get }) => {
        return addElementwise([get(forecastCashFlow),get(forecastEquityAccrued),get(forecastAppreciation),get(forecastReinvCashFlows)]);
    },
});

export const forecastTotalROI = selector({
    key: "forecastTotalROI",
    get: ({ get }) => {
        return get(forecastTotalReturn).map(x => x / Math.max(0,get(totalInvestment)));
    },
});

export const forecastCmAnnualizedReturn = selector({
    key: "forecastCmAnnualizedReturn",
    get: ({ get }) => {
        const arr = [];
        const cm_ret = [];
        const purch_price = parseFloat(get(purchasePriceState));
        const amt_fin = parseFloat(get(amountFinanced));
        const tot_ret_arr = get(forecastTotalReturn);
        const tot_inv = parseFloat(get(totalInvestment));

        for (let i=0; i < tot_ret_arr.length; i++){
            if (i===0){
                cm_ret[i] = tot_ret_arr[i];
            }
            else {
                cm_ret[i] = cm_ret[i-1] + tot_ret_arr[i];
            }
            arr[i] = ((cm_ret[i] + purch_price - amt_fin) / Math.max(0,tot_inv)) ** (1 / (i + 1)) - 1; // assuming you get the difference between purchase price and financed amount back, repairs and ARV flow through appreciation
        }
        return arr;
    },
});